export default ({properties: { columns, minCardWidth, cards, cardHeight }}) => {
	const groupClassName = `data-set-grid data-set-grid-${columns}`;
	const itemClassName = `data-set-grid-item-${cardHeight}`;

	const gridstyle = {
		'gridTemplateColumns': `repeat(${columns}, minmax(${Math.floor(minCardWidth)}px, 1fr))`
	};

	return (
		<div className={groupClassName} style={gridstyle}>
			{
				cards.map(card => <div className={itemClassName}> {card} </div>)
			}
		</div>
	);
};
