import {t} from 'sn-translate';
import truncate from 'lodash/truncate';
import isEmpty from 'lodash/isEmpty';
import { GRID_GAP, DEFAULT_MAX_CARDS } from '../../constants';
import { contractGenerator, transformer, EVAM } from '@devsnc/sn-declarative-actions';

export const transformState = state => {
	const { title, emptyMessage, data, maxCardsPerRow } = state.properties;
	const properties = {
		...state.properties,
		title: t(truncate(title, {'length': 63 })),
		emptyMessage: t(truncate(emptyMessage, { 'length': 203 })),
		data,
		maxCardsPerRow: maxCardsPerRow > DEFAULT_MAX_CARDS ? DEFAULT_MAX_CARDS: maxCardsPerRow,
	};
	return {...state, properties};
};

export const getAdjustedColumns = (parentWidth, maxCardsPerRow, cardWidth) => {
	if (!parentWidth) return maxCardsPerRow;
	const maxColumns = Math.min(maxCardsPerRow, Math.floor(parentWidth/cardWidth));
	const requiredWidthWithGap = (maxColumns*cardWidth) + ((maxColumns-1)*GRID_GAP);
	return requiredWidthWithGap <= parentWidth ? maxColumns : maxColumns-1;
};

export const reEvaluatePageSize = (dataLength, columns) => {
	/*
	* When cards per row is not a multiple of pageSize, we round the page size to the nearest multiple(upper limit) of the cards per row .
	*/
	if( dataLength > 0 && dataLength % columns > 0 ){
		const pageSize = parseFloat(dataLength) + (parseFloat(columns) - parseFloat(dataLength) % parseFloat(columns));
		return pageSize;
	}
	return false;
};

export const transformDAClientActionToUXEvents = declarativeActions => {
	if (isEmpty(declarativeActions)) return {};

	const daAction = transformer(declarativeActions);
	return contractGenerator(daAction, {modelType: EVAM}) || {};
};
