import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './__sn-data-set-view-grid.scss';

createCustomElement('sn-data-set-view-grid', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		cards: {
			default: []
		},
		cardHeight: {
			default: 'lg',
			schema: {
				type: 'string',
				enum: ['sm', 'md', 'lg']
			}
		},
		minCardWidth: {
			default: 150,
			schema: {
				type: 'number'
			}
		},
		columns: {
			default: 4,
			schema: {
				type: 'number'
			}
		},
	},
	transformState: state => {
		const {columns} = state.properties;
		const properties = {
			...state.properties,
			columns: columns
		};
		return {...state, properties};
	}
});
