import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './__sn-data-set-view-list.scss';
import { DEF_CARD_WIDTH } from '../../constants';

createCustomElement('sn-data-set-view-list', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		cards: {
			default: []
		},
		minCardWidth: {
			default: DEF_CARD_WIDTH,
			schema: {
				type: 'number'
			}
		}
	}
});
