import '../sn-data-set-toolbar';
import '../sn-data-set-view-grid';
import '../sn-data-set-view-list';
import '@servicenow/now-pagination-control';
import '@devsnc/sn-declarative-actions';
import isEmpty from 'lodash/isEmpty';
import { getAdjustedColumns, reEvaluatePageSize } from './utils';
import { SN_DATA_SET_PAGINATION_BUTTON_CLICKED, GRID_VIEW, DEF_CARD_WIDTH } from '../../constants';
import {resolve} from '@devsnc/sn-evam-resolver';

const mediatorWrapper = (view) => <sn-declarative-mediator should-wrap-action={true}>{view}</sn-declarative-mediator>;

const DataSetTemplate = ({ title, toggleView, defaultView, sortConfig, filterConfig, View, paginationProps, hidePaginationControls }) => (
	<div className='now-data-set-root-element'>
		<DataSetHeader title={title}></DataSetHeader>
		<DataSetToolbar
			toggleConfig={ { 'visible' : toggleView , 'view' : defaultView } }
			sortConfig={sortConfig}
			filterConfig={filterConfig}>
		</DataSetToolbar>
		{ View }
		<PaginationView
			hidePaginationControls={hidePaginationControls}
			paginationProps={paginationProps}>
		</PaginationView>
	</div>
);

const DataSetHeader = ({title}) => {
	if( title ) {
		return (
			<div className='now-data-set-header'>
				<h1 className='now-data-set-title'>{title}</h1>
			</div>
		);
	}
};

const DataSetToolbar = ({toggleConfig, sortConfig, filterConfig}) => {
	if( toggleConfig.visible || sortConfig.visible || !filterConfig.hideFilter ) {
		return (
			<div className='now-data-set-toolbar'>
				<sn-data-set-toolbar
					toggleConfig={toggleConfig}
					sortConfig={sortConfig}
					filterConfig={filterConfig} >
				</sn-data-set-toolbar>
			</div>
		);
	}
};

const PaginationView = ({ hidePaginationControls, paginationProps }) => {
	if( !hidePaginationControls ) {
		return (
			<div className='now-data-set-pagination-root'>
				<now-pagination-control {...paginationProps} />
			</div>
		);
	}
};

export default (state, {dispatch}) => {
	const {defaultView, data, cardHeight, maxCardsPerRow, minCardWidth, emptyMessage, toggleView, hideFilter } = state.properties;
	let View;
	let selectedFilter = '';
	let isSelectedFilterValid = false;

	const filterConfig = !isEmpty(data) ? (data.available_filters || []) : [];

	selectedFilter = !isEmpty(data) ? (data.selected_filter || '') : '';

	//if selected filter is not present in filters, then show the default filter
	isSelectedFilterValid = filterConfig.some(filter => filter.sys_id === selectedFilter);

	if (!isSelectedFilterValid) {
		selectedFilter = !isEmpty(filterConfig) ? data.available_filters[0].sys_id : '';
	}

	let paginationProps = state.paginationProps;

	const properties = {
		...state.properties,
		filterConfig: { filterConfig, hideFilter, selectedFilter }
	};

	if ( isEmpty(data) || !Array.isArray(data.items) || !data.items.length ) {
		View =  <div className="now-data-set-empty-state">{emptyMessage}</div>;
		paginationProps.hideGoToNext =  true;
	} else {
		const dataItems = resolve(data.items);
		const pageSize = data.page_size;
		const pageCursor = data.page_cursor;
		let pageCursors = state.pageCursors;
		const { parentWidth } = state;
		const columns = getAdjustedColumns(parentWidth, maxCardsPerRow, minCardWidth);
		paginationProps.selectedPageSize = pageSize;

		if ( !pageCursors.includes(pageCursor) ) pageCursors[pageCursors.length] = pageCursor;

		if(toggleView || (!toggleView && defaultView == GRID_VIEW)){
			let revaluatedPageSize = reEvaluatePageSize(dataItems.length, columns);
			paginationProps.selectedPageSize = revaluatedPageSize;

			if ( pageSize < revaluatedPageSize ) {
				pageCursors.length = 1;//resetting pageCursors list on page resize
				dispatch( SN_DATA_SET_PAGINATION_BUTTON_CLICKED, { 'pageSize': revaluatedPageSize } );
			}
		}

		if( data.items.length <  pageSize || data.is_last_page ){//last page
			paginationProps.selectedPageSize = pageSize;
			paginationProps.total = pageSize * (pageCursors.length - 1);//setting pageSize, total for now-pagination-control to know its the last page
			if( pageCursors.length == 2 ){ //last page == first Page
				paginationProps.hideGoToPrevious =  true;
				paginationProps.hideGoToNext =  true;
			}
		}else{ //not last page
			paginationProps.hideGoToNext =  false;
			paginationProps.hideGoToPrevious =  false;
		}

		if ( defaultView === GRID_VIEW ) {
			let cardWidth = DEF_CARD_WIDTH;
			if ( !isEmpty(minCardWidth) || Math.floor(minCardWidth) > DEF_CARD_WIDTH ){
				cardWidth = minCardWidth;
			}
			View = <sn-data-set-view-grid cards={dataItems} cardHeight={cardHeight} columns={columns} minCardWidth={cardWidth}></sn-data-set-view-grid>;
		} else {
			View = <sn-data-set-view-list cards={dataItems} minCardWidth={DEF_CARD_WIDTH}></sn-data-set-view-list>;
		}
	}

	return mediatorWrapper(<DataSetTemplate View={View} paginationProps={paginationProps} {...properties} />);
};
