import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import { LIST_VIEW, NOW_BUTTON_ICONIC_CLICKED, TOGGLE_BUTTON_CLICKED } from '../../constants';

createCustomElement('sn-data-set-viewtoggle', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		view: {
			default: LIST_VIEW
		},
		label: {
			default: ''
		}
	},
	actionHandlers: {
		[NOW_BUTTON_ICONIC_CLICKED]: ({ updateProperties, action: { payload: { view } }, dispatch }) => {
			updateProperties({ view });
			dispatch(TOGGLE_BUTTON_CLICKED, { view: view });
		}
	}
});
