import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import { LIST_VIEW } from '../../constants';
import styles from './__sn-data-set-toolbar.scss';
import { validateOptions } from './helper';
import rtlBehavior from '@servicenow/behavior-rtl';

createCustomElement('sn-data-set-toolbar', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		toggleConfig: {
			default: {
				visible: false,
				view: LIST_VIEW
			}
		},
		sortConfig: {
			default: {
				visible: false,
				options: []
			}
		},
		filterConfig: {
			default: {},
			schema: {
				type: 'object'
			}
		}
	},
	transformProps(state) {
		const { sortConfig, filterConfig: { filterConfig, hideFilter, selectedFilter } } = state.properties;
		const properties = {
			...state.properties,
			sortConfig: validateOptions(sortConfig),
			filterConfig: validateOptions(filterConfig),
			hideFilter,
			selectedFilter
		};
		return { ...state, properties };
	},
	behaviors: [
		{
			behavior: rtlBehavior,
		}
	]
});
