import { actionTypes } from '@servicenow/ui-core';
import get from 'lodash/get';
import { TOGGLE_BUTTON_CLICKED, SN_DATA_SET_PAGINATION_BUTTON_CLICKED, NOW_DATA_SET_PAGINATION_BUTTON_CLICKED, DA_WRAPPED_CLICKED,
	NOW_DROPDOWN_ITEM_CLICKED, NOW_DATA_SET_FILTER_ITEM_CLICKED, NOW_PAGINATION_CONTROL_SELECTED_PAGE_SET, DECLARATIVE_ACTION_SCRIPT_SUCCESS, 
	DECLARATIVE_ACTION_SCRIPT_FAILED, NOW_DATA_SET_ACTION_SUCCEEDED, NOW_DATA_SET_ACTION_FAILED, DECLARATIVE_ACTION,
	ACTION_TYPE_SERVER_SCRIPT } from '../../constants';

const getActionPayload = (state, updateState, payload, meta) => {
	try {
		const actionsInProcess = state.actionsInProcess;
		const assignmentId = meta.request.updatedUrl.split('declarative_action')[1].slice(1);
		const systable = meta.request.params.sysparm_table;
		const sysId = meta.request.data.sysId;
		const key = `${assignmentId}_${systable}_${sysId}`;
		const emitPayload = actionsInProcess.get(key);
		actionsInProcess.delete(key);
		emitPayload.declarativeAction_meta = meta;
		emitPayload.declarativeAction_payload = payload;
		updateState({actionsInProcess});
		return emitPayload;
	} catch (e) {
		return;
	}
};

export const actionHandlers = {
	[TOGGLE_BUTTON_CLICKED]: ({ updateProperties, action: { payload: { view } } }) => {
		updateProperties({defaultView: view});
	},
	[actionTypes.COMPONENT_RENDERED]: ({action: {payload: {host}}, updateState}) => {
		/** 
		 * DEF0126555: UXF sn-layout uses slot for grid layouts, component that puts into slot will have parentElement as sn-layout, not each grid item. 
		 * host.getBoundingClientRect().width will represent component grid container width(in decimals) as it gets put into the slot.
		 */
		const hostName = host.tagName;
		if (hostName === 'NOW-DATA-SET') {
			host.style.removeProperty('min-width');
		}
		
		const parentWidth = host.getBoundingClientRect().width;
		updateState({parentWidth});
	},
	[DA_WRAPPED_CLICKED]: ({dispatch, action: {payload}}) => {
		dispatch(payload.wrapped_action_name, payload.wrapped_payload);
	},
	[NOW_PAGINATION_CONTROL_SELECTED_PAGE_SET]: ({ host, action, dispatch, state, updateState }) => {
		const { pageCursors } = state;
		const { value } = action.payload;
		let pageNumber = value + 1;

		const payload = {...action.payload, pageCursor: pageCursors[value], pageNumber};

		const currentPage = {...state.paginationProps, selectedPage : value };
		updateState({
			path: 'paginationProps',
			value: currentPage,
			operation: 'set',
			shouldRender: false
		});

		const getDeclarativeMediatorElement = get(host, 'shadowRoot.lastChild');
		const getDataSetRootElement  = getDeclarativeMediatorElement.querySelector('.now-data-set-root-element');
		getDataSetRootElement.scrollIntoView();

		dispatch(NOW_DATA_SET_PAGINATION_BUTTON_CLICKED, payload);
	},
	[SN_DATA_SET_PAGINATION_BUTTON_CLICKED]: ({ action, dispatch }) => {
		dispatch(NOW_DATA_SET_PAGINATION_BUTTON_CLICKED, action.payload);
	},
	[NOW_DROPDOWN_ITEM_CLICKED]: ({ action, dispatch, state, updateState }) => {
		//reset pagination props when filter option is selected
		const defaultPage = { ...state.paginationProps, selectedPage: 0, hideGoToNext: true, hideGoToPrevious: true };

		updateState({
			path: 'paginationProps',
			value: defaultPage,
			operation: 'set',
			shouldRender: false
		});

		// Resetting the pageCursors using splice and push since there is no direct operation.
		const pageCursorsSize = state.pageCursors.length; 
		updateState({
			path: 'pageCursors',
			deleteCount: pageCursorsSize,
			operation: 'splice',
			shouldRender: false
		});
		updateState({
			path: 'pageCursors',
			value: 'init',
			operation: 'push',
			shouldRender: false
		});

		const payload = {...action.payload, pageCursor: 'init', pageNumber: 0};
		dispatch(NOW_DATA_SET_FILTER_ITEM_CLICKED, payload);
	},
	[DECLARATIVE_ACTION_SCRIPT_SUCCESS]: ({ state, dispatch, updateState, action: { payload, meta } } ) => {
		const emitPayload = getActionPayload(state, updateState, payload, meta);
		if (!payload) return;
		dispatch(NOW_DATA_SET_ACTION_SUCCEEDED, emitPayload);
	},
	[DECLARATIVE_ACTION_SCRIPT_FAILED]: ({ state, dispatch, updateState, action: { payload, meta } } ) => {
		const emitPayload = getActionPayload(state, updateState, payload, meta);
		if (!payload) return;
		dispatch(NOW_DATA_SET_ACTION_FAILED, emitPayload);
	},
	[DECLARATIVE_ACTION]: ({state, updateState, action: { payload: { action, model }}}) => {
		try {
			if (action.actionType !== ACTION_TYPE_SERVER_SCRIPT) return;
			const actionsInProcess = state.actionsInProcess;
			const assignmentId = action.assignmentId;
			const systable = model.table;
			const sysId = model.sysId;
			const key = `${assignmentId}_${systable}_${sysId}`;
			actionsInProcess.set( key, { action, model });
			updateState({actionsInProcess});
		} catch (e) {
			return;
		}			
	}
};