import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import isEmpty from 'lodash/isEmpty';
import view from './view';
import { LIST_VIEW, GRID_VIEW, DEF_ITEMS_PER_PAGE, DEF_CARD_WIDTH, DEF_NO_DATA_MSG, DEF_CARDS_PER_ROW, DEFAULT_MAX_CARDS } from '../../constants';
import styles from './now-data-set.scss';
import { transformState, transformDAClientActionToUXEvents } from './utils';
import rtlBehavior from '@servicenow/behavior-rtl';
import { actionHandlers } from './actions';

/**
* A list of records within a portal that offer users configurable view types and filter options. 
*
* ## now-data-set
*
* @summary Displays a list of records in either the default list view or grid view.
* @seismicElement now-data-set
*/
createCustomElement('now-data-set', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		/**
		* Header message to display.
		* @type {string}
		*/
		title: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Message displayed if the the data set is empty.
		* @type {string}
		*/
		emptyMessage: {
			default: DEF_NO_DATA_MSG,
			schema: {
				type: 'string'
			}
		},
		/**
		 *@type {enum}
		 * The size of the cards to display, applicable to grid view only.
		 */
		cardHeight: {
			default: 'sm',
			schema: {
				type: 'string',
				enum: ['sm', 'md', 'lg']
			}
		},
		/**
		* Data input in JSON format, generally from a data resource.
		* @type {object}
		*/
		data: {
			default: null,
			schema: {
				type: 'object'
			}
		},
		/**
		* When true, a toggle is visible to allow the user to switch between grid and list view.
		* @type {boolean}
		*/
		toggleView: {
			default: true,
			schema: {
				type: 'boolean'
			}
		},
		/**
		 *@type {enum}
		 * Allows the user to switch between grid and list view.
		 */
		defaultView: {
			default: LIST_VIEW,
			schema: {
				type: 'string',
				enum: [LIST_VIEW, GRID_VIEW]
			}
		},
		/**
		* not yet supported
		* @private
		*/
		sortConfig: {
			default: {
				visible: false,
				options: []
			},
			schema: {
				type: 'object'
			}
		},
		/**
		 *@type {enum}
		 * While in grid view, the maximum number of cards displayed per row.
		 * Default: 4. Maximum: 6.
		 */
		maxCardsPerRow: {
			default: DEF_CARDS_PER_ROW,
			schema: {
				type: 'number',
				enum: ['1', '2','3', DEF_CARDS_PER_ROW,'5', DEFAULT_MAX_CARDS]
			}
		},
		/**
		* While in grid view, the minimum width of each card.
		* @type {number}
		*/
		minCardWidth: {
			default: DEF_CARD_WIDTH,
			schema: {
				type: 'number'
			}
		},
		/**
		* When true, the filter dropdown is visible.
		* @type {boolean}
		*/
		hideFilter: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},
		/**
		* When true, pagination buttons are visible.
		* @type {boolean}
		*/
		hidePaginationControls: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},
		/**
		 * Object containing the data needed for dynamic declarative action event mapping in UI Builder.
		 * @type {DaClientActionContract}
		 */
		daClientActionContract: {
			computed({ properties: { data } }) {
				const declarativeActions = !isEmpty(data) && Array.isArray(data.all_actions) ? data.all_actions : [];
				return transformDAClientActionToUXEvents(declarativeActions);
			},
			schema: {
				type: 'object'
			},
			selectable: true
		}
	},
	initialState: {
		parentWidth: null,
		maxItemsPerPage: DEF_ITEMS_PER_PAGE,
		pageCursors: ['init'], //random string as passing null doesnot refresh the databroker while paginating back to first page,
		actionsInProcess: new Map(),
		paginationProps: {
			hideGoToFirst: true,
			hideGoToLast: true,
			hidePageNumbers: true,
			hidePageSizeControl: true,
			hideRange: true,
			hidePages: true,
			hideGoToNext:  true,
			hideGoToPrevious:  true,
			selectedPage: 0,
			selectedPageSize: DEF_ITEMS_PER_PAGE
		}
	},
	transformState,
	actionHandlers,
	dispatches: {
		/**
		 * Dispatched when the pagination button is clicked.
		 * @type {{}}
		 * @uib.description Dispatched when the pagination button is clicked
		 */
		NOW_DATA_SET_PAGINATION_BUTTON_CLICKED: {schema: {type: 'object'}},
		/**
		 * Dispatched when an item in the filter dropdown is selected.
		 *
		 * @type {{}}
		 */
		NOW_DATA_SET_FILTER_ITEM_CLICKED: {},
		/**
		 * Dispatched with information about the component level action when a server-side action is successful.
		 *
		 * @type {{}}
		 */
		NOW_DATA_SET_ACTION_SUCCEEDED: {},
		/**
		 * Dispatched with information about the component level action when a server-side action fails.
		 *
		 * @type {{}}
		 */
		NOW_DATA_SET_ACTION_FAILED: {}

	},
	behaviors: [
		{
			behavior: rtlBehavior,
		}
	]
});
