export default ({properties: { cards, minCardWidth }}) => {
	const groupClassName = 'data-set-list data-set-grid data-set-grid-1';

	const gridstyle = {
		'gridTemplateColumns': `repeat(1, minmax(${Math.floor(minCardWidth)}px, 1fr))`
	};

	return (
		<div className={groupClassName} style={gridstyle}>
			{ cards.map(card => (<div className='data-set-list-item'>{card}</div>)) }
		</div>
	);
};
