import isEmpty from 'lodash/isEmpty';

export const validateOptions = (config) => {
	//sort and filter props have different structure for config
	let options = config.options ? config.options : config;

	options = options.filter((option) => option.label && !isEmpty(option.label)).map((option) => (
		{...option, id: option.sys_id || option.id }
	));

	return config.options ? { ...config, options} : { options };
};

export const isEmptyString = (selectedFilter) => {
	return selectedFilter === undefined ||
		selectedFilter === null ||
		selectedFilter.trim().length === 0;
};
