import '@servicenow/now-dropdown';
import '@servicenow/now-label-value';
import { t } from 'sn-translate';
import '../sn-data-set-viewtoggle';
import { FALSE, TOGGLE_LABEL, SORT_LABEL, FILTER_LABEL } from '../../constants';
import isEmpty from 'lodash/isEmpty';
import lowerCase from 'lodash/lowerCase';
import kebabCase from 'lodash/kebabCase';
import get from 'lodash/get';
import { isEmptyString } from './helper';

const getTranslations = (options) => options.map((opt) => ({...opt, label: t(opt.label)}));

const DataSetItem = (props) => {
	const { dropdown, config: { visible, options, view, selectedFilter }, label } = props;

	//check empty options for filterConfig and sortConfig
	if (dropdown && isEmpty(options)) {
		return;
	}

	//check visible property for toggleConfig, sortConfig and filterConfig
	if ((!visible || lowerCase(String(visible)) === FALSE)) {
		return;
	}

	if (dropdown) {
		let defaultSelected = get(options[0], 'id');

		//Get the most recent value and update the default selected filter
		if (!isEmptyString(selectedFilter)) {
			defaultSelected = selectedFilter;
		}

		const className = `data-set-item ${kebabCase(label)}`;
		return (
			<div className = {className}>
				<now-label-value-inline label={label}></now-label-value-inline>
				<now-dropdown
					items={getTranslations(options)}
					select='single'
					selected-items={[defaultSelected]}
					size='md'
					variant='secondary'
					config-aria={{ 'aria-label': label }}>
				</now-dropdown>
			</div>
		);
	}

	return (
		<div className='data-set-item'>
			<now-label-value-inline label={label}></now-label-value-inline>
			<sn-data-set-viewtoggle view={view} label={`${label} options`} />
		</div>
	);
};

export default (state) => {
	const { toggleConfig, filterConfig, sortConfig, hideFilter, selectedFilter } = state.properties;
	
	let filterToggleConfig = {
		...filterConfig,
		visible: !hideFilter,
		selectedFilter
	};

	return (
		<div className='data-set-toolbar'>
			<DataSetItem label={t(FILTER_LABEL)} config={filterToggleConfig} dropdown={true}/>
			<DataSetItem label={t(SORT_LABEL)} config={sortConfig} dropdown={true} />
			<DataSetItem label={t(TOGGLE_LABEL)} config={toggleConfig} />
		</div>
	);
};

