import '@servicenow/now-button';
import { t } from 'sn-translate';
import { GRID_VIEW, LIST_VIEW } from '../../constants';
import isEmpty from 'lodash/isEmpty';

const GRID_LABEL = t('View information in grid mode'), LIST_LABEL = t('View information in list mode');
const getConfigAria = (type, selected, label = '') => {
	const labelTranslated = isEmpty(label) ? '' : `${t(label)}, `;
	
	return {
		'aria-label': `${labelTranslated}${type === GRID_VIEW ? GRID_LABEL : LIST_LABEL}`,
		'aria-pressed': selected.toString()
	};
};

export default ({ properties: { view, label } }) => {
	//prevent invalid view prop input
	if (view !== GRID_VIEW && view !== LIST_VIEW)
		view = LIST_VIEW;

	return (
		<div>
			<now-button-iconic
				size='md'
				variant={view === GRID_VIEW ? 'primary' : 'secondary'}
				append-to-payload={{ view: GRID_VIEW }}
				icon='grid-four-fill'
				tooltip-content={GRID_LABEL}
				config-aria={getConfigAria(GRID_VIEW, view === GRID_VIEW, label)}>
			</now-button-iconic>
			<now-button-iconic
				size='md'
				variant={view === LIST_VIEW ? 'primary' : 'secondary'}
				append-to-payload={{ view: LIST_VIEW }}
				icon='list-fill'
				tooltip-content={LIST_LABEL}
				config-aria={getConfigAria(LIST_VIEW, view === LIST_VIEW, label)}>
			</now-button-iconic>
		</div>
	);
};
